ul,
li,
ol {
  list-style-type: none;
  padding: 0;
  margin-bottom: 0;
}

label {
  margin-bottom: 0;
}
label .hs-error-msgs {
  color: red;
}
label .hs-error-msg {
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1em;
  color: red;
}

.hs-main-font-element h1 {
  line-height: 1.5rem;
}
.hs-form-field {
  position: relative;
  margin-bottom: 1.5rem;
}
.hs-form-field label {
  font-family: "Eina03", sans-serif;
  text-align: left;
  position: relative;
  top: 0.5rem;
  margin-bottom: 8px;
}
.hs-form-field label span {
  margin-bottom: 0;
  font-family: "Eina03", sans-serif;
  color: white;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.5em;
}
.hs-form-field input {
  width: 100%;
  height: 56px;
  padding: 10px 20px 10px 16px;
  border-style: none;
  border-radius: 5px;
  font-size: 1rem;
  font-family: "Eina03", sans-serif;
  background-color: white;
  color: #000;
}
.hs-form-field input:focus {
  border: 1px solid #f80;
}
.hs-form-field input:focus-visible {
  border: 1px solid #f80;
  outline: none;
}
.hs-form-field input .multi-container {
  display: flex;
  column-gap: 1rem;
}
.hs-form-checkbox-display {
  display: flex;
  align-items: center;
  padding: 0;
}
.hs-form-checkbox-display input {
  width: 1rem;
  height: 1rem;
  margin: 0 0.5rem 0 0;
  border-radius: 0.125rem;
  border: 1px solid #ccc;
}
.hs-form-checkbox-display span {
  margin-bottom: 0;
}
.hs-submit {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}
.hs-button {
  border: 1px solid #f80;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  color: #f80;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: auto;
  max-height: none;
  min-height: 3rem;
  min-width: 100px;
  padding: 1rem 24px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 4px;
  font-family: "Eina03", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  cursor: pointer;
  background-color: transparent;
}
.hs-button:hover {
  background-color: rgba(255, 136, 0, 0.15);
}